/*  
  Phosphor Core
*/
.p-DockPanel,
.p-DockPanel-widget {
  z-index: 0; }

.p-DockPanel-widget {
  border: 2px solid #848589;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px; }

.p-DockPanel-tabBar {
  z-index: 1; }

.p-DockPanel-handle {
  z-index: 2; }
  .p-DockPanel-handle.p-mod-hidden {
    display: none !important; }
  .p-DockPanel-handle:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: ""; }
  .p-DockPanel-handle[data-orientation="horizontal"] {
    cursor: ew-resize; }
  .p-DockPanel-handle[data-orientation="vertical"] {
    cursor: ns-resize; }
  .p-DockPanel-handle[data-orientation="horizontal"]:after {
    left: 50%;
    min-width: 8px;
    transform: translateX(-50%); }
  .p-DockPanel-handle[data-orientation="vertical"]:after {
    top: 50%;
    min-height: 8px;
    transform: translateY(-50%); }

.p-DockPanel-overlay {
  z-index: 3;
  box-sizing: border-box;
  pointer-events: none; }
  .p-DockPanel-overlay.p-mod-hidden {
    display: none !important; }

.p-DockPanel {
  position: relative;
  display: block; }

.p-DockPanel-overlay {
  background: rgba(184, 203, 190, 0.4);
  border: 1px dashed #848589; }
  .p-DockPanel-overlay .p-mod-root-top,
  .p-DockPanel-overlay .p-mod-root-left,
  .p-DockPanel-overlay .p-mod-root-right,
  .p-DockPanel-overlay .p-mod-root-bottom,
  .p-DockPanel-overlay .p-mod-root-center {
    border-width: 2px; }

.p-DockTabPanel {
  padding-right: 2px;
  padding-bottom: 2px; }
  .p-DockTabPanel > .p-StackedPanel {
    padding: 10px;
    background: #d5e1d0;
    border: 1px solid #848589;
    border-top: none; }

.p-DockPanel-tabBar .p-TabBar-tab {
  cursor: move; }

